import _ from 'lodash'
import axios from 'axios'
import { BASE_URL } from './config'

axios.defaults.baseURL = BASE_URL

export default function configInterceptor () {
  axios.interceptors.request.use(config => {
    return config
  }, err => {
    return Promise.reject(err)
  })

  axios.interceptors.response.use(
    (response) => {
      if (_.isPlainObject(response.data)) {
        const statusCode = response.data.status
        if (statusCode === '0') {
          return _.get(response, 'data.data')
        } else if (statusCode === '-1') {
          window.rootApp && window.rootApp.$router.replace({ name: 'Login' })
          return Promise.reject(response.data)
        } else {
          const err = _.get(response, 'data.msg') || '服务器错误，请稍后重试'
          window.rootApp && window.rootApp.$message.error(err)
          return Promise.reject(response.data)
        }
      } else {
        return response.data
      }
    },
    (error) => {
      window.rootApp && window.rootApp.$message.error(error.message)
      return Promise.reject(error)
    }
  )
}
