<template>
  <div class="c-pagination">
    <el-pagination
      background
      :current-page="current"
      :page-sizes="[3, 10, 20, 50 ,100, 150]"
      :page-size="pageSize"
      layout="slot, prev, pager, next, sizes, jumper"
      :total="total"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    >
      <span class="total">
        共{{ Math.ceil(total / pageSize) }}页/{{ total }}条数据
      </span>
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: 'CPagination',
  props: {
    pageSize: {
      type: Number,
      default: 10
    },
    total: {
      type: Number,
      default: 0
    },
    current: {
      type: Number,
      default: 1
    }
  },
  methods: {
    handleSizeChange (arg) {
      this.$emit('size-change', arg)
    },
    handleCurrentChange (arg) {
      this.$emit('current-change', arg)
    }
  }
}
</script>

<style lang="stylus">
.c-pagination
  display flex
  justify-content flex-end
  padding 20px 0
  .el-pagination
    .el-pagination__jump
      margin-left 0
    .el-pager li
      line-height 26px
      border 1px solid #D8DBE7
      background-color transparent
      &:hover
        color red
        border-color $admin-theme-color
      &:not(.disabled).active
        border-color $admin-theme-color
    .total
      color #606266
</style>
