<template>
  <el-table
    :data="data"
    border
  >
    <el-table-column
      v-for="o in options"
      :key="o.prop || o.type"
      align="center"
      header-align="center"
      :prop="o.prop"
      :label="o.label"
      :width="o.width"
      :min-width="o.minWidth"
    >
      <template slot-scope="scope">
        <span v-if="o.type === 'index'">{{data.indexOf(scope.row) + 1 + (pageIndex - 1) * pageSize}}</span>
        <div v-else-if="o.type === 'switch'">
          <el-switch
            @change="handleAction($event, { type: 'switch', prop: o.prop, item: scope.row })"
            :value="scope.row[o.prop]"
          />
        </div>
        <div :class="['custom-table-actions-wrapper', { 'signle': o.actions && o.actions.length === 1 }]" v-else-if="o.type === 'process'">
          <span
            v-for="action in scope.row.actions || o.actions"
            :key="action.title"
            @click="handleAction($event, { type: 'action', action, prop: o.prop, item: scope.row })"
          >
            {{action.title}}
          </span>
        </div>
        <span v-else>{{scope.row[o.prop]}}</span>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'CustomTable',
  props: {
    pageIndex: {
      type: Number,
      default: 0
    },
    pageSize: {
      type: Number,
      default: 20
    },
    data: {
      type: Array,
      default () {
        return []
      }
    },
    options: {
      type: Array,
      default () {
        return []
      }
    }
  },
  methods: {
    handleAction (newValue, options) {
      console.log(options)
      this.$emit('tableColumnEvent', newValue, options)
    }
  }
}
</script>

<style lang="stylus" scoped>
.custom-table-actions-wrapper
  span
    padding 0 5px
    display inline-block
    user-select none
    cursor: pointer;
    color $admin-theme-color
    &:hover
      color #4899D0
  &.signle
    span
      padding 5px 16px
      border-radius 4px
      color #ffffff
      background-color $admin-theme-color
      &:hover
        background-color #4899D0
</style>
