import _ from 'lodash'
export default {
  computed: {
    _matchedRouteVMs () {
      const matched = this.$route ? this.$route.matched : []
      return matched.map(m => {
        return _.get(m, 'instances.default') || null
      })
    },
    _currentRouteIndex () {
      const matchedVMs = this._matchedRouteVMs
      return matchedVMs.indexOf(this)
    },
    hasSubRoute () {
      const index = this._currentRouteIndex
      return index !== -1 && index < this._matchedRouteVMs.length - 1
    },
    parentRouteVM () {
      const index = this._currentRouteIndex
      return index < 1 ? null : this._matchedRouteVMs[index - 1]
    }
  },
  watch: {
    _matchedRouteVMs (vms) {
      if (_.last(vms) === this) {
        const routeReappear = this.$options.routeReappear
        routeReappear && routeReappear.call(this)
      }
    }
  }
}
