<template>
  <el-form ref="generateForm" class="c-generate-form" :model="models" :rules="rules" :label-width="labelWidth" :inline="inline">
    <generate-form-item
      v-for="item in items"
      :key="item.key"
      v-model="item.model"
      :item="item"
      :models="models"
      @select-visible-change="$emit('select-visible-change', $event, item)"
    >
      <slot slot="front" :name="item.model + '-front'" />
      <slot :name="item.model" />
    </generate-form-item>
    <el-form-item v-if="buttons.length > 0" class="c-generate-form-actions">
      <el-button
        v-for="b in buttons"
        :key="b.title"
        :type="b.type || 'primary'"
        @click="handleButton(b)"
      >
        {{ b.title }}
      </el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import GenerateFormItem from './GenerateFormItem'
export default {
  name: 'CGenerateForm',
  components: { GenerateFormItem },
  props: {
    items: {
      type: Array,
      default () {
        return []
      }
    },
    rules: {
      type: Object,
      default () {
        return {}
      }
    },
    models: {
      type: Object,
      default () {
        return {}
      }
    },
    inline: {
      type: Boolean,
      default: false
    },
    labelWidth: {
      type: String,
      default: ''
    },
    buttons: {
      type: Array,
      default () {
        return []
      }
    }
  },
  methods: {
    handleButton (button) {
      this.$emit(button.emit)
    },
    generateForm () {
      return this.$refs.generateForm
    }
  }
}
</script>

<style lang="stylus">
.c-generate-form
  &-item
    margin-bottom 16px
  &-actions
    display block !important
    .el-button
      width 104px
</style>
