<template>
  <el-form-item class="c-generate-form-item" :class="item.customClass" :label="item.label" :prop="item.model">
    <slot name="front" />
    <template v-if="item.type === 'EMPTY'">
      <div class="c-generate-form-item-empty"></div>
    </template>
    <template v-if="item.subType === 'SPAN'">
      <span class="c-generate-form-item-sub-span">{{ subDateModel }}</span>
    </template>
    <template v-if="item.type === 'SPAN'">
      <span :class="item.customSpanClass">{{ item.spanValue || dataModel }}</span>
    </template>
    <template v-else-if="item.type === 'NUMBER'">
      <number-input v-model.trim="dataModel" :disabled="item.disabled" :min="item.min" :decimal="item.decimal" :max="item.max" :size="item.size" :placeholder="item.placeholder" />
    </template>
    <template v-else-if="item.type === 'INPUT'">
      <el-input v-model.trim="dataModel" :type="item.inputType || 'text'" :disabled="item.disabled" :maxlength="item.maxlength" :size="item.size" :placeholder="item.placeholder" />
    </template>
    <template v-else-if="item.type === 'INPUTRANGE'">
      <input-range v-model="dataModel" :decimal="item.decimal" />
    </template>
    <template v-else-if="item.type === 'TEXTAREA'">
      <el-input v-model.trim="dataModel" :rows="item.rows" type="textarea" :placeholder="item.placeholder" />
    </template>
    <template v-else-if="item.type === 'RADIO_GROUP'">
      <el-radio-group v-model="dataModel">
        <el-radio v-for="(r, index) in item.radios" :key="index" :label="r.label">{{ r.title }}</el-radio>
      </el-radio-group>
      <p v-if="item.desc" class="desc">{{ item.desc }}</p>
    </template>
    <template v-else-if="item.type === 'SELECT'">
      <el-select v-model="dataModel" :multiple="item.multiple" :size="item.size" :loading="item.loading" @visible-change="$emit('select-visible-change', $event)">
        <el-option v-for="option in item.options" :key="option.value" :label="option.label" :value="option.value" />
      </el-select>
    </template>
    <template v-else-if="item.type === 'CHECKBOX_GROUP'">
      <el-checkbox-group v-model="dataModel">
        <el-checkbox v-for="o in item.checkboxs" :key="o.value" :disabled="o.disabled" :label="o.value">{{ o.label }}</el-checkbox>
      </el-checkbox-group>
    </template>
    <template v-else-if="item.type === 'DATERANGE'">
      <el-date-picker
        v-model="dataModel"
        :size="item.size"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
      />
    </template>
    <template v-else-if="item.type === 'UPLOADIMAGE'">
      <upload-image v-model="dataModel" />
    </template>
    <slot />
  </el-form-item>
</template>

<script>
import InputRange from './form-item/InputRange'
import UploadImage from './upload-image/UploadImage'

export default {
  name: 'CGenerateFormItem',
  components: { InputRange, UploadImage },
  props: {
    item: {
      type: Object,
      default () {
        return {}
      }
    },
    models: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  data () {
    return {
      dataModel: this.models[this.item.model],
      subDateModel: this.models[this.item.subModel]
    }
  },
  watch: {
    dataModel: {
      deep: true,
      handler (val) {
        this.models[this.item.model] = val
        this.$emit('update:models', {
          ...this.models,
          [this.item.model]: val
        })
        this.$emit('input-change', val, this.item.model)
      }
    },
    models: {
      deep: true,
      handler (val) {
        this.dataModel = val[this.item.model]
      }
    }
  }
}
</script>

<style lang="stylus">
.c-generate-form-item
  &-sub-span
    display inline-block
    margin-right 10px
  .desc
    font-size 14px
    color rgba(153,153,153,1)
    line-height 20px
</style>
