<template>
  <div id="rootApp" class="c-page-layout" :class="{ 'hide-nav': !showNavs }">
    <div v-if="showNavs" class="c-page-layout-menu-1">
      <div class="logo">
        CMC
      </div>
      <ul class="menu-1-wrapper">
        <li
          v-for="one in menus"
          :key="one.name"
          class="menu-1-item"
          :class="{ selected: oneRouteName === one.name }"
          @click="handleTap(one)"
        >
          <span>{{ one.zh }}</span>
        </li>
      </ul>
    </div>
    <div class="c-page-layout-content-wrapper">
      <div v-if="showNavs" class="c-page-layout-menu-2">
        <template v-for="two in currentOne.children">
          <div class="menu-2-header" :class="{ selected: two.name === twoRouteName }" :key="two.name" @click="handleTap(two)">
            {{ two.zh }}
          </div>
        </template>
      </div>
      <div class="c-page-layout-content">
        <router-view />
      </div>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
import { mapGetters } from 'vuex'

export default {
  name: 'CPageLayout',
  props: {
    routes: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      menus: []
    }
  },
  computed: {
    ...mapGetters('user', ['userInfo']),
    showNavs () {
      if (!this.oneRouteName) return false
      const hides = ['Login']
      return !hides.includes(this.oneRouteName)
    },
    oneRouteName () {
      if (this.$route.matched.length) {
        return this.$route.matched[0].name
      } else {
        return null
      }
    },
    twoRouteName () {
      if (this.$route.matched.length > 1) {
        return this.$route.matched[1].name
      } else {
        return null
      }
    },
    currentOne () {
      return _.find(this.menus, { name: this.oneRouteName }) || {}
    }
  },
  mounted () {
    const routes = _.cloneDeep(this.routes)
    routes.forEach(one => {
      one.children && one.children.forEach(two => {
        two.extend = true
      })
    })
    this.menus = routes
  },
  methods: {
    handleTap (menu) {
      let m = menu
      while (m.children && m.children.length > 0) {
        const sub = m.children[0]
        if (!sub.zh) break
        m = sub
      }
      if (this.$route.name === m.name) return
      this.$router.push({
        name: m.name
      })
    }
  }
}
</script>

