<template>
  <div class="b-form-item-input-range">
    <number-input v-model="min" v-bind="$attrs" />
    <span />
    <number-input v-model="max" v-bind="$attrs" />
  </div>
</template>

<script>
export default {
  name: 'BFormItemInputRange',
  inheritAttrs: false,
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      min: this.value[0] || '',
      max: this.value[1] || ''
    }
  },
  computed: {
    range () {
      return [
        this.min, this.max
      ]
    }
  },
  watch: {
    value (val) {
      this.min = this.value[0] || ''
      this.max = this.value[1] || ''
    },
    range () {
      this.$emit('change', this.range)
    }
  }
}
</script>

<style lang="stylus">
.b-form-item-input-range
  display flex
  align-items center
  .el-input
    width 100px
    input
      padding-right 8px
  span
    width 30px
    height 2px
    background #979797
    margin 0 8px
</style>
