<template>
  <el-form ref="customForm" class="c-custom-form" :model="models" :inline="true">
    <div class="c-custom-form-title">筛选</div>
    <div class="c-custom-form-content">
      <generate-form-item
        v-for="item in otherItems"
        :key="item.key"
        v-model="item.model"
        :item="item"
        :models="models"
        :class="[item.align]"
        @select-visible-change="$emit('select-visible-change', $event, item)"
      >
        <slot :name="item.model" />
      </generate-form-item>
      <div class="space"></div>
      <div class="c-custom-form-right-input-item" v-if="rightInput">
        <generate-form-item
          class="right-input-item"
          v-model="rightInput.model"
          :item="rightInput"
          :models="models"
        />
        <el-form-item class="c-custom-form-actions">
          <el-button type="primary" @click="handleButton">筛选</el-button>
        </el-form-item>
      </div>
    </div>
  </el-form>
</template>

<script>
import _ from 'lodash'
import GenerateFormItem from './GenerateFormItem'
export default {
  name: 'CCustomForm',
  components: { GenerateFormItem },
  props: {
    items: {
      type: Array,
      default () {
        return []
      }
    },
    rules: {
      type: Object,
      default () {
        return {}
      }
    },
    models: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  computed: {
    otherItems () {
      return this.items.filter(i => i.type !== 'RIGHTINPUT')
    },
    rightInput () {
      const item = _.find(this.items, { type: 'RIGHTINPUT' })
      return { ...item, type: 'INPUT' }
    }
  },
  methods: {
    handleButton () {
      this.$emit('filter')
    },
    customForm () {
      return this.$refs.customForm
    }
  }
}
</script>

