import Vue from 'vue'
import VueRouter from 'vue-router'

import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

import orderRoutes from '@/views/order/routes'
import courseRoutes from '@/views/course/routes'
import productRoutes from '@/views/product/routes'

NProgress.configure({ showSpinner: false })
Object.defineProperty(VueRouter.prototype, 'routeBack', {
  get () {
    return function () {
      const matched = this.currentRoute.matched
      if (matched.length > 2) {
        this.replace({ name: matched[matched.length - 2].name })
      }
    }
  }
})

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    {
      name: 'indexRedirect',
      path: '/',
      redirect: '/course/mine'
    }
  ]
})

router.addRoutes(orderRoutes)
router.addRoutes(productRoutes)
router.addRoutes(courseRoutes)

router.beforeEach((to, from, next) => {
  NProgress.start()
  next()
})

router.afterEach((to, from) => {
  NProgress.done()
})

export function fetchRoutes () {
  return [
    ...courseRoutes,
    ...productRoutes,
    ...orderRoutes
  ]
}

export default router
